.btn {
    border: 1px solid #3399ff;
    font-family: inherit;
    font-size: inherit;
    color: #ff66ff;
    background: none;
    cursor: pointer;
    padding: 10px 5px;
    display: inline-block;
    margin: 5px 5px;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: 700;
    outline: none;
    position: relative;
    -webkit-transition: all 1s;
    -moz-transition: all 1s;
    transition: all 1s;
    width:250px
}

.btn:hover {
    color: #ffccff;
}

.btn::after {
    content: '';
    position: absolute;
    z-index: -1;
    -webkit-transition: all 1s;
    -moz-transition: all 1s;
    transition: all 1s
}

.btn:after {
    width: 0%;
    height: 100%;
    top: 0;
    left: 0;
    background: #ff5050;
}

.btn:hover:after,
.btn:active:after {
    width: 100%
}

